<template>
  <div style="text-align: left;width: fit-content;">
    <div>
      <span v-highlight>
          <pre>
            <code class="code">{{ code }}</code>
          </pre>
      </span>
      <span v-highlight>
          <pre>
            <code class="code">{{ mdCode }}</code>
          </pre>
        </span>
      <span v-highlight>
          <pre>
            <code class="code">{{ java }}</code>
          </pre>
        </span>
      <span v-highlight>
          <pre class="html">
            <code class="code">{{ html }}</code>
          </pre>
        </span>
      <div>
        <markdown></markdown>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: "index",
  data() {
    return {
      code: "#include<iostream> \n" +
          "using namespace std; \n" +
          "int main{ \n" +
          "std::cout << \" hello World \" << endl;  \n" +
          "return 0; \n" +
          "}",
      mdCode: "# hello world\n" +
          "\n" +
          "you can write text [with links](http://example.com) inline or [link references][1].\n" +
          "\n" +
          "* one _thing_ has *em*phasis\n" +
          "* two __things__ are **bold**\n" +
          "\n" +
          "[1]: http://example.com\n" +
          "\n" +
          "---\n" +
          "\n" +
          "hello world\n" +
          "===========\n" +
          "\n" +
          "<this_is inline=\"xml\"></this_is>\n" +
          "\n" +
          "> markdown is so cool\n" +
          "\n" +
          "    so are code segments\n" +
          "\n" +
          "1. one thing (yeah!)\n" +
          "2. two thing `i can write code`, and `more` wipee!",
      java: "\n" +
          "@Test\n" +
          "  void testGetAll(){\n" +
          "    List stus = stuService.queryAllStu();\n" +
          "    stus.forEach(e -> {\n" +
          "    System.out.println(e);\n" +
          "     });\n" +
          "}\n" +
          "      ",
      html: "<dialog>\n" +
          "  <header>Headsup</header>\n" +
          "  <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna.</p>\n" +
          "  <button type=\"button\" class=\"js-close-dialog\">Close</button>\n" +
          "</dialog>"
    }
  }
}
</script>

